import React, { Component } from 'react'
import Img from '../assets/logo.png'
import {Link} from 'react-router-dom'

export default class Expensereceipt extends Component {
    constructor(props){
        super(props)
        this.state = {
            admin: this.props.location.state.admin,
            employee: this.props.location.state.employee
        }
    }
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <Link className='nav-link' style={{color:'black'}} to={{
                            pathname: "/dashboard",
                            state: {
                                employee: this.props.location.state.employee,
                                admin: this.state.admin
                            }
                            }}>Return to Dashboard</Link>
                        </li>
                        <li className='nav-item'>
                            <span><button className='btn btn-danger' style={{borderRadius:'30px', color:'white'}} onClick={()=>window.print()}>Print</button></span>
                        </li>
                    </ul>

                </div>
            </nav>
                <div className='receipt'> 
                <div className='receipt-div'>
                <div className='container'>
                    <img src={Img} alt='' className='receipt-img'/><br/>
                    Suite 25, comfort shoping plaza 1,<br/>
                    Etete Road,
                    G.R.A,
                    Benin City,<br/>
                    Edo State,
                    Nigeria.<br/>
                    +2348023541175<br/>
                    <strong>Date:</strong> {this.props.location.state.date}<br/>
                    <strong>Employee:</strong> {this.state.employee}
                    <hr style={{border:'1px dashed black'}}></hr>
                </div>
                <div className='container'>
                    <strong>Receipt No:</strong> {this.props.location.state.receiptnumber}<br/>
                    <strong>Recipient:</strong> {this.props.location.state.recipient}<br/>
                    <strong>Description:</strong> {this.props.location.state.description}<br/>
                    <strong>Amount:</strong> {'₦'+this.props.location.state.amount}<br/>
                    <strong>Department:</strong> {this.props.location.state.department}<br/>
                    <hr style={{border:'1px dashed black'}}></hr>
                    <div className='receipt-total'>
                        <h2>Total: <strong> {'₦'+this.props.location.state.amount}</strong></h2>
                        
                    </div>
                </div>
                {/* <div className='receipt-link'>
                </div> */}
            </div>
            </div>
            </div>
            
        )
    }
}