import React, { Component } from 'react'
import axios from 'axios'
import '../App.css'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'


export default class Expenses extends Component {
    constructor(props){
        super(props)
        this.state = {
            total:0,
            recipient:'',
            amount:'',
            description:'',
            employee:'',
            department:'',
            show: false,
            success:'',
            id:'',
            message:'',
            eprops: this.props.location.state.employee,
            admin: this.props.location.state.admin
        }
        this.data = []
        this.customers = []
        this.submit = this.submit.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    async componentDidMount(){
        var data = []
        var url3 = '/expensereceipts'
        axios.get(url3)
        .then((res) => {
            for(var i = 0; i < res.data.length; i++){
                data.push(res.data[i])
                this.setState({data: res.data[i], total: this.state.total + res.data[i].amount})
            }
        })
        this.data = data
    }

    onChange(e){
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    submit(event){
        event.preventDefault()
        this.setState({
            message: "Saving..."
        })
        const details = {
          recipient: this.state.recipient,
          amount: this.state.amount,
          description: this.state.description,
          employee: this.state.eprops,
          department: this.state.department,
          id: this.state.id
        }
        const url = '/editexpense'
        axios.post(url, details)
        .then((res) => {
            this.setState({
                message:'Saved successfully'
            })
            console.log(res.data)})
        .catch(error => this.setState({success: false}))
        
            setTimeout(()=>{
                this.setState({
                    message:'',
                    show: false
                })
              }, 5000)


        var data = []
        var url3 = '/expensereceipts'
        axios.get(url3)
        .then((res) => {
            for(var i = 0; i < res.data.length; i++){
                data.push(res.data[i])
                this.setState({data: res.data[i], total: this.state.total + res.data[i].amount})
            }
        })
        this.data = data
    }

    render(){
        return(
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/dashboard',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Dashboard</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/expense',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Add Expense</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/createadmin',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Create Admin</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/chart',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Sales Chart</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>

            <div className='container' style={{marginTop: '5%', display: this.state.show ? 'block' : 'none'}}>
                
                  <h2 style={{textAlign:'center'}}>Edit Expense</h2><br/>
                  <div style={{textAlign:'center'}}>
                     <span style={{color:"green", textAlign:"center", alignItems:'center'}}>{this.state.message}</span>
                  </div>
                  <div className='form-div'>
                  <span style={{color:'red'}}></span>
                      <form onSubmit={this.submit}>
                       Recipient:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Recipient'
                      onChange = {this.onChange}
                      value = {this.state.recipient}
                      className = 'form-control form-group'
                      name = 'recipient'
                      />
                      Description:
                      <textarea required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Expense Description'
                      onChange = {this.onChange}
                      value = {this.state.description}
                      className = 'form-control form-group'
                      name = 'description'
                      />
                      Amount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Amount'
                      onChange = {this.onChange}
                      value = {this.state.amount}
                      className = 'form-control form-group'
                      name = 'amount'
                      />
                      Department:
                      <select required onChange = {this.onChange} name="department" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select>
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>
              <div className='container'><br/><br/>
                  <h2 style={{textAlign:'center', marginTop:'10%'}}>Expenses</h2><br/>
                  <ReactHTMLTableToExcel
                    id="export"
                    className="btn btn-success"
                    table="table"
                    filename="Table"
                    sheet="tablexls"
                    buttonText="Export"
                    />
                <table id='table' className='container'>
                    <tbody>
                    <tr>
                        <th>Date</th>
                        <th>Recipient</th>
                        <th className='hide'>Description</th>
                        <th>Amount</th>
                        <th>Employee</th>
                        <th className='hide'>Department</th>
                        <th>Edit</th>
                        <th className='print hide'>Print</th>
                    </tr>
                {
                    this.data.map(dat => {
                        return(
                            <tr key={dat._id}>
                                <td>{dat.date}</td>
                                <td>{dat.recipient}</td>
                                <td className='hide'>{dat.description}</td>
                                <td>{'₦' + dat.amount}</td>
                                <td>{dat.employee}</td>
                                <td className="hide">{dat.department}</td>
                                <td><button className='btn btn-success' onClick = {()=>{
                                    this.setState({
                                        show:true,
                                        description: dat.description,
                                        amount: dat.amount,
                                        date: dat.date,
                                        recipient: dat.recipient,
                                        department: dat.department,
                                        id: dat._id
                                    })
                                }}>Edit</button></td>
                                <td className='print hide'><Link to={{
                                        pathname: '/expensereceipt',
                                        state: {
                                            admin: this.props.location.state.admin, 
                                            department: dat.department, 
                                            employee: dat.employee, 
                                            date: dat.date,
                                            description: dat.description,
                                            receiptnumber: dat.receiptnumber,
                                            recipient: dat.recipient,
                                            amount: dat.amount
                                        }
                                        }}><button className='btn btn-danger' id='print'>Print</button>
                                </Link></td>
                            </tr> 
                         ) 
                    }).reverse()
                }
                <tr>
                    <td><strong>Total</strong></td>
                    <td>{this.data.length}</td>
                    <td className='hide'></td>
                    <td>{'₦' + Math.floor(this.state.total)}</td>
                    <td className="hide"></td>
                    <td className='hide'></td>
                    <td></td>
                    <td></td>
                </tr>
                </tbody>
                </table><br/><br/><br/>
                
            </div>
            </div>
            
        )
    }
}
