import React, { Component } from 'react'
import axios from 'axios'
import '../App.css'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'


export default class Receipts extends Component {
    constructor(props){
        super(props)
        this.state = {
            units: '',
            message:'',
            service: '',
            date: '',
            price: '',
            customer: '',
            id: '',
            success: '',
            discount:'',
            phone: '',
            employee:'',
            aprice:'',
            total:0,
            sdate:'',
            sdate1:'',
            number:'',
            department:'',
            show:false,
            eprops: this.props.location.state.employee,
            admin: this.props.location.state.admin,
        }
        this.data = []
        this.customers = []
        this.submit = this.submit.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    async componentDidMount(){
        console.log(this.props)
        var data = []
        // var url = 'https://tslposapi.herokuapp.com/receipts'
        // var url2 = 'http://localhost:5000/receipts'
        var url3 = '/receipts'
        axios.get(url3)
        .then((res) => {
            for(var i = 0; i < res.data.length; i++){
                data.push(res.data[i])
                this.setState({data: res.data[i], total: this.state.total + res.data[i].price})
            }
        })
        this.data = data
    }

    submit(e){
        e.preventDefault()
        this.setState({
            message: "Saving..."
        })
        const details = {
            service: this.state.service,
            units: this.state.units,
            date: this.state.date,
            customer: this.state.customer,
            department: this.state.department,
            phone: this.state.phone,
            price: this.state.price,
            discount: this.state.discount,
            id: this.state.id
        }
        const url = '/edittransaction'
        axios.post(url, details)
        .then((res) => {
            this.setState({
                message:'Saved successfully'
            })
            console.log(res.data)})
        .catch(error => this.setState({success: false}))


        setTimeout(()=>{
            var data = []
            var url3 = '/receipts'
            axios.get(url3)
            .then((res) => {
                for(var i = 0; i < res.data.length; i++){
                    data.push(res.data[i])
                    this.setState({data: res.data[i], total: this.state.total + res.data[i].price})
                }
            })
            this.data = data
          }, 1000)
        
            setTimeout(()=>{
                this.setState({
                    message:'',
                    show: false
                })
              }, 5000)


    }

    onChange(e){
        this.setState({
            [e.target.name]: e.target.value
        })
    }

        

    render(){
        return(
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/dashboard',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Dashboard</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/createadmin',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Create Admin</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/chart',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Sales Chart</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>

            <div className='container' style={{marginTop: '10%', display: this.state.show ? 'block' : 'none'}}>
                  <h2 style={{textAlign:'center'}}>Initialize Receipt</h2><br/>
                  <div className='form-div'>
                  <span style={{color:'green'}}>{this.state.message}</span>
                      <form onSubmit={this.submit}>
                      Department:
                      <select required onChange = {this.onChange} name="department" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select>
                      Service:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Service'
                      onChange = {this.onChange}
                      value = {this.state.service}
                      className = 'form-control form-group'
                      name="service"
                      />
                      Units:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Units'
                      onChange = {this.onChange}
                      value = {this.state.copies}
                      name="units"
                      className = 'form-control form-group'
                      />
                       Customer:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Customer'
                      onChange = {this.onChange}
                      value = {this.state.customer}
                      className = 'form-control form-group'
                      name="customer"
                      />
                      Phone Number:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Phone Number'
                      onChange = {this.onChange}
                      value = {this.state.phone}
                      className = 'form-control form-group'
                      name="phone"
                      />
                       Price:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Price'
                      onChange = {this.onChange}
                      value = {this.state.price}
                      className = 'form-control form-group'
                      name="price"
                      />
                      Discount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Discount'
                      onChange = {this.onChange}
                      value = {this.state.discount}
                      className = 'form-control form-group'
                      name="discount"
                      />
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>


              <div className='container' ><br/><br/>
                  <h2 style={{textAlign:'center', marginTop:'10%'}}>Transactions </h2><br/>
                  <ReactHTMLTableToExcel
                    id="export"
                    className="btn btn-success btn-md"
                    table="table"
                    filename="Table"
                    sheet="tablexls"
                    buttonText="Export"/>
                <table id='table' className='container'>
                    <tbody>
                    <tr>
                        <th>Date</th>
                        <th>Customer</th>
                        <th className='hide'>Phone Number</th>
                        <th>Service</th>
                        <th className='hide'>Units</th>
                        <th className='hide'>Employee</th>
                        <th className='hide'>Discount</th>
                        <th className='hide'>Department</th>
                        <th>Price Paid</th>
                        <th>Edit</th>
                        <th className='print hide'>Print</th>
                    </tr>
                {
                    this.data.map(dat => {
                        return(
                            <tr key={dat._id}>
                                <td>{dat.date}</td>
                                <td>{dat.customer}</td>
                                <td className='hide'>{dat.phone}</td>
                                <td>{dat.session}</td>
                                <td className='hide'>{dat.copies}</td>
                                <td className='hide'>{dat.employee}</td>
                                <td className='hide'>{dat.discount + '%'}</td>
                                <td className='hide'>{dat.department}</td>
                                <td>{'₦' + dat.price}</td>
                                <td><button className="btn btn-success" onClick = {()=>{
                                    this.setState({
                                        show:true,
                                        service: dat.session,
                                        units: dat.copies,
                                        date: dat.date,
                                        customer: dat.customer,
                                        phone: dat.phone,
                                        price: dat.price,
                                        discount: dat.discount,
                                        id: dat._id
                                    })
                                }}>Edit</button></td>
                                <td className='print hide'><Link to={{
                                        pathname: '/print',
                                        state: {
                                            session: dat.session,
                                            date: dat.date,
                                            price: dat.price,
                                            customer: dat.customer,
                                            id: dat.receipt,
                                            discount: dat.discount,
                                            aprice: dat.aprice,
                                            employee: dat.employee,
                                            phone: dat.phone,
                                            copies: dat.copies,
                                            department: dat.department,
                                            admin: this.state.admin
                                        }
                                        }}><button className='btn btn-danger' id='print'>Print</button>
                                </Link></td>
                            </tr> 
                         ) 
                    }).reverse()
                }
                <tr>
                    <td><strong>Total</strong></td>
                    <td>{this.data.length}</td>
                    <td className='hide'></td>
                    <td></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td>{'₦' + Math.floor(this.state.total)}</td>
                    <td></td>
                    <td className="hide"></td>
                </tr>
                </tbody>
                </table><br/><br/><br/>
                
            </div>
            </div>
            
        )
    }
}
