import React, { Component } from 'react'
import {Bar} from 'react-chartjs-2'
import axios from 'axios'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'

export default class Chart extends Component {
    constructor(props){
        super(props)
        this.state = {
            chartData:{
                labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                datasets: [
                    {
                        label: '2021 Sales',
                        data: [],
                        backgroundColor: "green",
                        borderColor: 'black'
                    }
                ],
            },
            employee: this.props.location.state.employee,
            admin: this.props.location.state.admin
        }
    }

    componentDidMount(){
        axios.get('/getchartdata')
        .then((res) => {
            this.setState(prevState => ({
                chartData:{
                    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                    datasets: [
                        {
                            label: 'Sales',
                            data: [res.data.data.jan,res.data.data.feb,res.data.data.mar,res.data.data.apr,res.data.data.may,res.data.data.jun,res.data.data.jul,res.data.data.aug,res.data.data.sep,res.data.data.oct,res.data.data.nov,res.data.data.dec],
                            backgroundColor: "green",
                            borderColor: 'pink'
                        },
                        {
                            label: 'Expenses',
                            data: [res.data.data2.jan,res.data.data2.feb,res.data.data2.mar,res.data.data2.apr,res.data.data2.may,res.data.data2.jun,res.data.data2.jul,res.data.data2.aug,res.data.data2.sep,res.data.data2.oct,res.data.data2.nov,res.data.data2.dec],
                            backgroundColor: "red",
                            borderColor: 'pink'
                        }
                    ],
                }
            }))
            console.log(res.data)
        })
    }

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/receipts',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>View Receipts</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/createadmin',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Create Admin</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/dashboard',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Dashboard</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/chart',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Sales Chart</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>

                <div className='chart' style={{marginTop:'10%'}}>
                    <Bar
                        data={this.state.chartData}
                        options={{
                            maintainAspectRatio: false,
                            responsive: true
                        }}
                        style={{
                            height:'100vh'
                        }}
                    />
               </div>
               <br/><br/><br/>
            </div>
            
        )
    }
}
