import React, { Component } from 'react'
import {Link, Redirect} from 'react-router-dom'
import axios from 'axios'
import Img from '../assets/logo.png'


export default class Expense extends Component {
    constructor(props){
        super(props)
        this.state = {
            service: '',
            department: '',
            date: '',
            recipient: '',
            success: '',
            logout: '',
            id: '',
            amount:'',
            description: '',
            employee: this.props.location.state.employee,
            aprice: '',
            copies:'',
            admin: this.props.location.state.admin,
            receiptnumber:''
        }
        this.submit = this.submit.bind(this)
        this.logout = this.logout.bind(this)
        this.changeService = this.changeService.bind(this)
        this.changeDepartment = this.changeDepartment.bind(this)
        this.changeRecipient = this.changeRecipient.bind(this)
        this.changeAmount = this.changeAmount.bind(this)
        this.changeDescription = this.changeDescription.bind(this)
        
    }

    changeDepartment(event){
        this.setState({department: event.target.value})
      }

      changeRecipient(event){
        this.setState({recipient: event.target.value})
      }

      changeAmount(event){
        this.setState({amount: event.target.value})
      }

      changeService(event){
        this.setState({service: event.target.value})
      }

      changeDescription(event){
        this.setState({description: event.target.value})
      }

    submit(event){
        event.preventDefault()
        const details = {
          recipient: this.state.recipient,
          amount: this.state.amount,
          description: this.state.description,
          employee: this.state.employee,
          department: this.state.department
        }
        var url3 = '/addexpense'
        axios.post(url3, details)
        .then((res) => {this.setState({success: true, department: res.data.department, dscription: res.data.description, date: res.data.date, recipient: res.data.recipient, amount: res.data.amount, receiptnumber: res.data.receiptnumber}); console.log(res.data)})
        .catch(error => this.setState({success: false}))
    }  

    logout(){
        var url3 = '/logout'
        axios.get(url3)
        .then(res => this.setState({logout: res.data}))
        .catch(error => this.setState({logout: false}))
    }

    

    render() {
      if(this.state.admin === 'Super Admin'){
        return (
          <div>
            <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/dashboard',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Dashboard</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/receipts',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>View Receipts</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/createadmin',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Create Admin</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/chart',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Sales Chart</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/expenses',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Expenses</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>

              <div className='container' style={{marginTop: '5%'}}>
                  <h2 style={{textAlign:'center'}}>Add Expense</h2><br/>
                  <div className='form-div'>
                  <span style={{color:'red'}}></span>
                      <form onSubmit={this.submit}>
                       Recipient:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Recipient'
                      onChange = {this.changeRecipient}
                      value = {this.state.recipient}
                      className = 'form-control form-group'
                      />
                      Description:
                      <textarea required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Expense Description'
                      onChange = {this.changeDescription}
                      value = {this.state.description}
                      className = 'form-control form-group'
                      />
                      Amount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Amount'
                      onChange = {this.changeAmount}
                      value = {this.state.amount}
                      className = 'form-control form-group'
                      />
                      Department:
                      <select required onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select>
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>
              {
               this.state.logout === true ? <Redirect to='/'/> : null
              }
              {
               this.state.success === true ? <Redirect to={{pathname:'/expensereceipt', 
               state:{
                    admin: this.state.admin, 
                    department: this.state.department, 
                    employee: this.state.employee, 
                    date: this.state.date,
                    description: this.state.description,
                    receiptnumber: this.state.receiptnumber,
                    recipient: this.state.recipient,
                    amount: this.state.amount,           
            }}}/> : null
              }
          </div>
      )
      }else if(this.state.admin === 'Regular Admin'){
        return (
          <div>
            <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/> TslNigeria</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/dashboard',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Dashboard</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>
              <div className='container' style={{marginTop: '5%'}}>
                  <h2 style={{textAlign:'center'}}>Add Expense</h2><br/>
                  <div className='form-div'>
                  <span style={{color:'red'}}></span>
                      <form onSubmit={this.submit}>
                      Recipient:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Recipient'
                      onChange = {this.changeRecipient}
                      value = {this.state.recipient}
                      className = 'form-control form-group'
                      />
                      Description:
                      <textarea required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Expense Description'
                      onChange = {this.changeDescription}
                      value = {this.state.description}
                      className = 'form-control form-group'
                      />
                      Amount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Amount'
                      onChange = {this.changeAmount}
                      value = {this.state.amount}
                      className = 'form-control form-group'
                      />
                      Department:
                      <select required onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select>
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>
              {
               this.state.logout === true ? <Redirect to='/'/> : null
              }
              {
               this.state.success === true ? <Redirect to={{pathname:'/expensereceipt', 
               state:{
                    admin: this.state.admin, 
                    department: this.state.department, 
                    employee: this.state.employee, 
                    date: this.state.date,
                    description: this.state.description,
                    receiptnumber: this.state.receiptnumber,
                    recipient: this.state.recipient,
                    amount: this.state.amount,           
            }}}/> : null
              }
          </div>
      )
      }else{
        return(
          <Redirect to='/'/>
        )
        
      }
        
    }
}
